import React, {FC} from "react"
import {
    Box, Divider, Typography, List,
    Button, IconButton,
    SwipeableDrawer
} from "@mui/material";
import {
    Close as CloseIcon,
    ProductionQuantityLimits as ProductionQuantityLimitsIcon,
} from "@mui/icons-material";

import Item from "./Item";
import {cartAtom, openCartDrawerAtom} from "../../../store";
import {useAtom} from "jotai";

interface ICProps {
    window?: () => Window

}

const CartDrawer: FC<ICProps> = ({window}) => {
    const container = window !== undefined ? () => window().document.body : undefined;

    const [cart, setCart] = useAtom(cartAtom)
    const [openCartDrawer, setOpenCartDrawer] = useAtom(openCartDrawerAtom)


    const totalCoast = cart
        .reduce((acc: number, cur) => (acc + (cur.price * cur.amount)), 0)
        .toFixed(2)

    const elements = cart.map((item) => (
        <Item key={item.id} {...item}/>
    ))

    const cartTitle = cart.length ? `Корзина (${cart.length})` : `Корзина`;

    const handleOpen = () => {
        setOpenCartDrawer(true)
    }
    const handleClose = () => {
        setOpenCartDrawer(false)
    }

    return (
        <SwipeableDrawer
            container={container}
            variant="temporary"
            open={openCartDrawer}
            onClose={handleClose}
            onOpen={handleOpen}
            anchor={'right'}
            ModalProps={{
                keepMounted: true, // Better open performance on mobile.
            }}
            sx={{
                '& .MuiDrawer-paper': {
                    boxSizing: 'border-box',
                    width: {xs: '100%', sm: 'calc(100vw - 50%)'},
                },
            }}
        >
            <Box sx={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'space-between',
                p: 2
            }}>
                <Typography variant="h6" sx={{my: 2}}>{cartTitle}</Typography>
                <IconButton
                    onClick={handleClose}
                    sx={{display: {xs: 'flex', sm: 'none'}}}
                >
                    <CloseIcon/>
                </IconButton>
            </Box>
            <Divider/>
            {elements.length ? <Box sx={{overflow: 'auto'}}>
                <Box sx={{textAlign: 'center'}}>
                    <List>
                        {elements}
                    </List>
                </Box>
                <Box sx={{
                    display: 'flex',
                    p: 2,
                    flexDirection: 'column'
                }}>
                    <Button variant="contained" sx={{mb: 1}}>Check Out (Total {totalCoast}$)</Button>
                    <Button variant="outlined">View Cart</Button>
                </Box>
            </Box> : <Box
            sx={{
                height: '100%',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                flexDirection: 'column',
            }}
            >
                <Typography>Корзина пуста</Typography>
                <ProductionQuantityLimitsIcon/>
            </Box>}

        </SwipeableDrawer>
    )
}

export default CartDrawer
