import React, {FC} from "react"
import {Box, Divider, Typography} from '@mui/material';
import {} from "@mui/icons-material";
import {Navigation} from "swiper";
import {Swiper, SwiperSlide} from "swiper/react";
import ProductCard, {ICProductCartPropsAttribute} from "../../../components/common/ProductCard";
import MainPageSectionTitle from "../../../components/common/MainPageSectionTitle";
import {productsMock} from "../../../mock";

interface ICProps {

}

const NewArrivals: FC<ICProps> = (props) => {
    return (
        <>
            <MainPageSectionTitle title="Новинки"/>

            <Box m={'-8px'} mb={'-14px'}>
                <Swiper
                    id="main-new-arrivals"
                    spaceBetween={0}
                    slidesPerView={1}
                    modules={[Navigation]}
                    navigation
                    breakpoints={{
                        450: {
                            slidesPerView: 2,
                        },
                        700: {
                            slidesPerView: 3,
                        },
                        900: {
                            slidesPerView: 4,
                        },
                        1200: {
                            slidesPerView: 5,
                        },
                    }}
                >
                    {
                        productsMock.reverse().map((element, key) => {
                            const imgUrl = `https://picsum.photos/1500/500?random=${key}`
                            return (
                                <SwiperSlide key={key}>
                                    <Box sx={{m: '8px', mb: '14px'}}>
                                        <ProductCard
                                            id={element.id}
                                            name={element.name}
                                            price={element.variant[0].price}
                                            discount={element.variant[0].discount}
                                            amount={element.variant[0].amount}
                                            img={element.img[0].src}
                                            attributes={[]}
                                            priceUnit={'грн.'}
                                        />
                                    </Box>
                                </SwiperSlide>
                            )
                        })
                    }
                </Swiper>
            </Box>
        </>
    )
}

export default NewArrivals
