import React, {FC} from "react"
import {
    Box,
    List,
    SwipeableDrawer,
} from "@mui/material";
import Item from "./Item";
import {useAtom} from "jotai";
import { openMenuDrawerAtom} from "../../../store";
import {pages} from "../../../mock/header";


interface ICProps {
    window?: () => Window
}

const MenuDrawer: FC<ICProps> = ({window}) => {
    const container = window !== undefined ? () => window().document.body : undefined;
    const [openMenuDrawer, setOpenMenuDrawer] = useAtom(openMenuDrawerAtom)

    const elements = pages.map((item) => (<Item key={item.name} {...item} parentLength={0}/>))

    const handleOpen = () => {
        setOpenMenuDrawer(true)
    }
    const handleClose = () => {
        setOpenMenuDrawer(false)
    }

    return (
        <SwipeableDrawer
            container={container}
            variant="temporary"
            open={openMenuDrawer}
            onClose={handleClose}
            onOpen={handleOpen}
            ModalProps={{
                keepMounted: true, // Better open performance on mobile.
            }}
            sx={{
                display: {xs: 'block', sm: 'none'},
                '& .MuiDrawer-paper': {boxSizing: 'border-box', width: 240},
            }}
        >
            <Box sx={{textAlign: 'center'}}>
                <Box>

                </Box>
                <List>
                    {elements}
                </List>
            </Box>
        </SwipeableDrawer>
    )
}

export default MenuDrawer
