import React, {FC,useState} from "react"
import {
    Avatar, Typography,
    Card, CardContent, CardMedia, CardActionArea, useTheme
} from '@mui/material';
import {} from "@mui/icons-material";
import DynamicIcon from "../../UI/DynamicIcon";

interface ICProps {
    title: string,
    icon: string,
    description: string,
    image?: string
}

const AdventureCard: FC<ICProps> = ({title, icon, description, image}) => {
    const theme =useTheme()
    const [mouseOver, setMouseOver] = useState(false)

    return (
        <Card
            raised={mouseOver}
            sx={{
                position: 'relative',
                textAlign: 'center',
                height: '100%',
            }}
        >
            <CardMedia

                sx={{
                    position: 'absolute',
                    height: '100%',
                    width: '100%',
                    opacity: mouseOver ? 0.5 : 0.15,
                    transition: 'opacity 250ms'
                }}
                image={`${process.env.PUBLIC_URL}/${image}`}
            />
            <CardActionArea
                sx={{
                    height: '100%'
                }}
                onMouseOver={()=>{setMouseOver(true)}}
                onMouseLeave={()=>{setMouseOver(false)}}
            >
                <Avatar
                    sx={{
                        m: '0 auto',
                        mt: 5,
                        mb: 4,
                        width: 80, height: 80,
                        backgroundColor: mouseOver ? theme.palette.primary.dark : theme.palette.primary.light
                    }}
                >
                    <DynamicIcon iconName={icon} fontSize="large"/>
                </Avatar>
                <CardContent
                    sx={{
                        backdropFilter: mouseOver ? "blur(2px)" : "none",
                    }}
                >
                    <Typography variant="h5" mb={2}>{title}</Typography>
                    <Typography variant="body1">{description}</Typography>
                </CardContent>
            </CardActionArea>
        </Card>
    )
}

export default AdventureCard
