import React from 'react';
import {Box, List, ListItem, ListItemButton, ListItemIcon, ListItemText, Typography, Grid} from "@mui/material";
import DynamicIcon from "../../UI/DynamicIcon";

const data = [
    {
        label: "Каталог",
        elements: [
            {icon: "ChevronRight", label: "Сережки", link: "#"},
            {icon: "ChevronRight", label: "Каблучки", link: "#"},
            {icon: "ChevronRight", label: "Підвіски", link: "#"},
            {icon: "ChevronRight", label: "Браслети", link: "#"},
            {icon: "ChevronRight", label: "Ланцюжки", link: "#"},
            {icon: "ChevronRight", label: "Аксесуари", link: "#"},
            {icon: "ChevronRight", label: "Кулони", link: "#"},
        ]
    },
    {
        label: "Додаткова інформація",
        elements: [
            {icon: "Quiz", label: "Питання та відповіді", link: "#"},
            {icon: "LocalShipping", label: "Доставка та Оплата", link: "#"},
            {icon: "PointOfSale", label: "Оптова співпраця", link: "#"},
            {icon: "Feedback", label: "Відгуки", link: "#"},
            {icon: "Contacts", label: "Контакти", link: "#"},
        ]
    },
    {
        label: "Приєднатися до нас",
        elements: [
            {icon: "Instagram", label: "Instagram", link: "https://www.instagram.com/neon_silver_"},
            {icon: "Facebook", label: "Facebook", link: "https://www.instagram.com/neon_silver_"},
            {icon: "Telegram", label: "Telegram", link: "https://t.me/neon_silver_925"},
        ]
    }

]
const FooterMenu = () => {
    return (
        <Grid
            container
            spacing={2}
            columns={{xs: 12, sm: 6, md: 4}}
        >
            {data.map(({label, elements}) => {
                return (
                    <Grid item key={label} flexGrow={1}>
                        <Typography mb={1}>{label}</Typography>
                        <List
                            disablePadding
                            dense
                        >
                            {elements.map((item) => <ListItem
                                dense
                                disablePadding
                            >
                                <ListItemButton
                                    href={item.link}
                                    target={"_blank"}
                                    dense
                                    sx={{
                                        paddingX: 1
                                    }}
                                >
                                    <ListItemIcon sx={{
                                        minWidth: '30px'
                                    }}>
                                        <DynamicIcon iconName={item.icon}/>
                                    </ListItemIcon>
                                    <ListItemText primary={item.label}/>
                                </ListItemButton>
                            </ListItem>)}
                        </List>
                    </Grid>
                )
            })}
        </Grid>
    );
};

export default FooterMenu;
