import React, {FC} from "react"
import {Box,Typography} from '@mui/material';

import {
    Navigation, Autoplay, Pagination,
    EffectCube, Parallax
} from "swiper";
import {Swiper, SwiperSlide} from "swiper/react";
import "swiper/css/effect-cube";

const data = [
    {
        image: "images/banner/banner_1-1.jpg"
    },
    {
        image: "images/banner/banner_1-2.jpg"
    },
    {
        image: "images/banner/banner_2-1.jpg"
    },
    {
        image: "images/banner/banner_3-1.jpg"
    },
    {
        image: "images/banner/banner_4-1.jpg"
    },
    {
        image: "images/banner/banner_5-1.jpg"
    },
]

interface ICProps {

}

const Banner: FC<ICProps> = (props) => {
    return (
        <Swiper
            id="main-banner"
            effect="cube"
            loop
            spaceBetween={0}
            slidesPerView={1}
            grabCursor
            modules={[Navigation, Pagination, Autoplay, EffectCube, Parallax]}
            navigation
            pagination
            autoplay={{
                delay: 3000,
                disableOnInteraction: false,
                pauseOnMouseEnter: true,
            }}
            cubeEffect={{
                shadow: true,
                slideShadows: true,
                shadowOffset: 15,
                shadowScale: 0.94,
            }}
            parallax
        >
            {
                data.map((element, key) => {
                    return (
                        <SwiperSlide key={key}>
                            <Box
                                style={{
                                    display: 'flex',
                                    justifyContent: 'center',
                                    overflow: 'hidden'
                                }}
                                sx={{
                                    height: {xs: 200, sm: 350, md: 450}
                                }}
                            >
                                <Box
                                    crossOrigin="anonymous"
                                    component={'img'}
                                    src={`${process.env.PUBLIC_URL}/${element.image}`}
                                    sx={{
                                        objectFit: 'cover',
                                        width: '100%'
                                    }}
                                />
                            </Box>
                        </SwiperSlide>
                    )
                })
            }
        </Swiper>
    )
}

export default Banner
