import {
    Box, Container, Typography, Divider
} from "@mui/material"
import React, {FC} from "react"
import FooterMenu from "./FooterMenu";
import LogoIcon from "../../common/LogoIcon";

interface ICProps {

}

const Footer: FC<ICProps> = () => {

    const currentYear = new Date().getFullYear()

    return (
        <Box
            component="footer"
            sx={{
                py: 3,
                px: 2,
                mt: 'auto',
                backgroundColor: (theme) =>
                    theme.palette.mode === 'light'
                        ? theme.palette.grey[200]
                        : theme.palette.grey[800],
            }}
        >
            <Container maxWidth="xl">
                <Box
                    sx={{
                        display: 'flex',
                        flexDirection: {xs: "column", md: "row"},
                        gap: 2
                    }}
                >
                    <Box>
                        <Divider>
                            <LogoIcon sx={{
                                transition: 'font-size 200ms',
                                fontSize: {
                                    xs: 100,
                                    md: 200
                                },
                                color: (theme) => theme.palette.text.primary

                            }}/>
                            <Typography
                                variant="h5"
                                component="div"
                                align={'center'}

                                sx={{
                                    position: 'relative',
                                    top: '-50%',
                                    display: {xs: 'none', md: 'block'},
                                    textShadow:(theme) =>{
                                        const color = theme.palette.mode === 'light'
                                                ? theme.palette.grey[200]
                                                : theme.palette.grey[800];
                                        return `0 0 4px ${color}, 0 0 15px ${color}`
                                    }
                                }}
                            >NeonSilver</Typography>
                        </Divider>
                    </Box>
                    {/*<Divider orientation="vertical" variant="middle" flexItem/>*/}
                    <FooterMenu/>
                </Box>
                <Divider sx={{
                    marginY: 2
                }}/>
                <Typography>&copy; Інтернет-магазин «NeonSilver», 2017-{currentYear}.</Typography>
            </Container>
        </Box>
    )
}

export default Footer
