import React, {FC,useState} from 'react';
import {Tab, Tabs, useScrollTrigger} from "@mui/material";
import {pages} from "../../../mock/header";

interface ICProps {
    window?: () => Window
}

const   MainMenu: FC<ICProps> = ({ window}) => {
    const [value, setValue] = useState(0);

    const handleChange = (event: React.SyntheticEvent, newValue: number) => {
        setValue(newValue);
    };

    const trigger = useScrollTrigger({
        target: window ? window() : undefined,
        disableHysteresis: true,
        threshold: 0,
    });

    const elements = pages.map(({name, link, child}) => {
        return (<Tab
            key={name}
            label={name}
            sx={{
                // paddingX: 0.5,
                paddingY: trigger ? 0.5 : 1,
                minHeight: 'auto',
                minWidth: 'auto'
            }}
            // icon={child.length ? <ExpandMore /> : undefined}
            // iconPosition="end"


            // component={item.external ? MaterialLink : Link}
            // href={item.external ? item.pathname : null}
            // to={item.external
            //     ? null
            //     : {pathname: item.pathname, search: this.props.location.search}
            // }
        />)
    })

    return (
        <Tabs
            value={value}
            onChange={handleChange}
            variant="scrollable"
            scrollButtons="auto"
            allowScrollButtonsMobile
            selectionFollowsFocus
            sx={{
                minHeight: 'auto',
                width: 'max-content',
                maxWidth: '100%',
                margin: '0 auto'
            }}
            TabIndicatorProps={{
                sx:{
                    height: '1px'
                }
            }}
            TabScrollButtonProps={{
                sx:{
                    '&.Mui-disabled': { opacity: 0.2 },
                }
            }}

        >
            {elements}
        </Tabs>
    );
};

export default MainMenu;
