import React from 'react';
import {
    Close as CloseIcon,
    Search as SearchIcon,
    OpenInNewOff  as OpenInNewOffIcon ,
} from "@mui/icons-material";
import {
    Dialog, DialogContent, DialogTitle,
    IconButton,
    Input, InputAdornment,
    Divider, Box, Typography,
} from "@mui/material";

const SearchBox = () => {
    const [open, setOpen] = React.useState(false);

    const [search, setSearch] = React.useState('')
    const [result, setResult] = React.useState([])


    const handleSearchChange = (str: string) => {
        setSearch(str)
    }
    const handleSearchClose = () => {
        setOpen(false)
    }

    return (
        <>
            <IconButton
                color="inherit"
                onClick={() => {
                    setOpen(true)
                }}
            >
                <SearchIcon/>
            </IconButton>
            <Dialog
                scroll="paper"
                onClose={handleSearchClose}
                open={open}
            >
                <DialogTitle>
                        <Input
                            autoFocus={true}
                            placeholder="Search..."
                            value={search}
                            onChange={(event)=>{
                                handleSearchChange(event.target.value)
                            }}
                            startAdornment={
                                <InputAdornment position="start">

                                    <SearchIcon/>
                                </InputAdornment>
                            }
                        />
                    <IconButton
                        sx={{
                            position: 'absolute',
                            top: 0,
                            right: 0
                        }}
                        onClick={handleSearchClose}
                    >
                        <CloseIcon/>
                    </IconButton>
                </DialogTitle>
                <Divider />
                <DialogContent>
                    <Box>
                        <Typography>
                            Results:
                        </Typography>
                    </Box>

                </DialogContent>
            </Dialog>
        </>
    );
};

export default SearchBox;
