import React, {FC, useEffect, useState} from "react"
import {
    Box,
    SpeedDial, SpeedDialIcon, SpeedDialAction, IconButton, Backdrop, Grow
} from '@mui/material';
import {} from "@mui/icons-material";

import {ICProductCartPropsAttribute} from "../index";
import DynamicIcon from "../../../UI/DynamicIcon";

interface ICProps {
    focused: boolean
    attributes: ICProductCartPropsAttribute[]
}

const Attributes: FC<ICProps> = ({focused, attributes}) => {

    if (!attributes.length) {
        return null;
    }
    const [open, setOpen] = useState(false)

    useEffect(() => {
        focused || setOpen(false)
    }, [focused])

    const elements = attributes.map((attribute, key) => {
        const options = Array.isArray(attribute.value) ? attribute.value.join(',') : attribute.value

        return (
            <Box
                key={key}
                sx={{display: 'flex', minWidth: '100px'}}
            >
                <DynamicIcon iconName={attribute.icon} sx={{mr: 2}}/>
                {options}
            </Box>
        )
    })
    return (
        <Box>
            <IconButton
                size="small"
                color={focused ? "primary": "default"}
                onClick={() => {
                    setOpen(prevState => !prevState)
                }}
            >
                <DynamicIcon iconName={!open ? 'QuestionMark' : 'Close'}/>
            </IconButton>
            <Grow in={open} style={{
                position: "absolute",
                top: 0,
                left: 0,
                width: '100%',
                height: '300px',
                padding: '10px',
                backdropFilter: 'blur(2px)',
                display: 'flex',
                alignItems: 'center',
                justifyContent:  'center',
                flexDirection: 'column'
            }}>
                <Box>
                    {elements}
                </Box>
            </Grow>
        </Box>
    )
}

export default Attributes
